import { Injectable } from '@angular/core';
import {BehaviorSubject, Observable} from "rxjs";
import {HttpClient, HttpParams} from "@angular/common/http";
import {environment} from "../../environments/environment";

@Injectable({
  providedIn: 'root'
})
export class VakantiesService {

	constructor(private http:HttpClient) { }
	getVakanties(search: any, pageNumber: number): Observable<any>{
		let queryParams = new HttpParams();
		if(search)
			Object.keys(search).forEach(function (key) {
				queryParams = queryParams.append(key, search[key]);
			});
		queryParams = queryParams.append('per_page', pageNumber);
		return this.http.get(environment.apiUrl + '/getTrips',{
			params: queryParams
		});
	}
	getTripTypes(): Observable<any>{
		let queryParams = new HttpParams();
		return this.http.get(environment.apiUrl + '/getTripTypes', {params: queryParams});
	}
}
