import { Component } from '@angular/core';
import { AuthService } from '../../services/auth/auth.service';

@Component({
  selector: 'app-guide-nav',
  templateUrl: './guide-nav.component.html',
  styleUrls: ['./guide-nav.component.scss']
})
export class GuideNavComponent {
	authUser: any;
	constructor(
		private authService: AuthService
	) {}
	
	ngOnInit() {
		this.authService.authorize().then(result => {
			if (result) {
				this.authService.user.subscribe((response: any) => {
					this.authUser = response;
				});
				
			}
		});
		
	}
	logout(event:any) {
		this.authService.logout().subscribe(res => {
			window.location.href = '/login.html';
		});
	}

}
