import { ChangeDetectorRef, Component, OnInit, ViewChild } from '@angular/core';
import { UntypedFormBuilder, Validators } from '@angular/forms';
import {PostCode} from "../validators/PostCode";
import {CheckDate} from "../validators/CheckDate";
import { DataService } from '../services/data.service';
import { BookingService } from '../services/booking.service';
import { ActivatedRoute, Router, RouterState } from '@angular/router';
import { HttpErrorResponse } from '@angular/common/http';
import { ResponseErrorComponent } from '../common/response-error/response-error.component';

@Component({
  selector: 'app-booking',
  templateUrl: './booking.component.html',
  styleUrls: ['./booking.component.scss']
})
export class BookingComponent implements OnInit{

	configurations: any = null;
	date: any;
	page_name: any = '';
	type: any = '';
	page: any;
	trip: any;
	bookingForm:any;
	booking:any;
	errors: any = [false];
	showToolTip = false;
	showExtraContactPerson = false;
	postCodeValidator:any;
	insurances:any = [];
	termsAndCondition:any = [];
	isSubmitted = false;
	underAge = false;
	activeLink = [true,false,false];
	pageLoading = false;
	cancellation_insurance = 0;
	travel_insurance = 0;
	ti_taxed = 0;
	ti_tax = 0;
	payment_selected = 1;
	insurance_administration_charges = 0;
	reservation_terms = false;
	reservation = false;
	reservation_accepted = false;
	reservation_validated = false;
	params: any;
	reservation_id: any;
	key: any;
	cancel = false;
  processing;

  @ViewChild(ResponseErrorComponent) responseErrorComponent!: ResponseErrorComponent;

  constructor(
	private route: ActivatedRoute,
	private router: Router,
	private fb: UntypedFormBuilder,
	private ref: ChangeDetectorRef,
	private dataService: DataService,
	private bookingService: BookingService,
  ) {
		this.bookingForm = this.bookingFormGroup('');
		this.processing = false;
  }

	ngOnInit(): void {
		this.dataService.configurationsSubject.subscribe((configurations: any) => {
			this.pageLoading = true;
		  if(configurations.settings){
			this.configurations = configurations;
			this.pageLoading = false;
			this.parsePath();
		  }
		});

		this.route.url.subscribe((url: any) => {
		  if(url.length > 0){
			const state: RouterState = this.router.routerState;
			this.type = this.route.snapshot.data ?? '';
			this.params = this.route.snapshot.queryParams;
			this.reservation_id = this.params.reservation_id;
			this.key = this.params.key;
			console.log(this.type);
			if(this.type.reserve){
				this.reservation = true;
			}
			this.date = url[1] + '';
			url.shift();
			url.shift();
			for(let part of url){
			  this.page_name += '/' + part.path.replace('.html', '')
			}
			this.page_name = this.page_name.slice(1);
		  }
		  this.parsePath();
		});
		this.ref.detectChanges();

	}

  parsePath(){
    if(!this.configurations){
      return;
    }
    //if empty
    if(this.page_name){
      this.configurations.pages.some((page: any) => {
        if(this.page_name === page.page_name){
          //Landing Pages
          if(page.type === 'location'){
			  if(!this.pageLoading){
			this.pageLoading = true;
			this.dataService.getTripData(page.id, this.date, this.reservation_id, this.key).subscribe((response: any) => {
				this.dataService.getCsrf();
				this.trip = response.trip;

				if (response.reservation) {
					this.bookingForm = this.bookingFormGroup(response.reservation);
					this.bookingForm.get('reservation_id').setValue(this.reservation_id);
				}
				this.bookingForm.get('trip_id').setValue(this.trip.id);
				this.page = response.page;
				this.insurance_administration_charges = this.configurations.config.insurance_administration_charges.value;

					this.cancellation_insurance = this.trip.trip_fee * this.configurations.config.cancelation_fees_percent.value / 100;
					this.cancellation_insurance = Number(this.cancellation_insurance + Number(this.configurations.config.insurance_administration_charges.value));
					this.cancellation_insurance = this.cancellation_insurance + ((this.cancellation_insurance*21)/100);

					this.travel_insurance = this.page.pageable.destination.travel_insurance_fees * this.trip.duration;
					this.ti_taxed = (this.travel_insurance * 1.44)/100;
					this.ti_tax = ( (this.ti_taxed * 21) / 100 );
					this.travel_insurance = this.travel_insurance + this.ti_tax;


					  this.pageLoading = false;
					  this.dataService.setMeta({
						"title": this.page.meta_title,
						"description": this.page.meta_description,
						"keywords": this.page.meta_keywords,
						"date": this.page.updated_at
					  });
            }, (error: any) => {
              //this.router.navigate(['/']);
            });

            return true;
			  }
          }
        }
        return false;
      });
    }
  }
  splittedDate: any = [];
  bookingFormGroup(data: any) {
	  if(data && data.child_dob){
		  this.splittedDate = data.child_dob.split("-");
	  }
    return this.fb.group({
	  trip_id: [''],
      child_firstname: [data.child_firstname ?? '', Validators.required],
      child_lastname: [data.child_lastname ?? '', Validators.required],
	  gender: [data.gender ?? '', Validators.required],
	  dob_date: [this.splittedDate[2] ?? '', Validators.required],
      dob_month: [this.splittedDate[1] ?? parseInt('1'), Validators.required],
      dob_year: [this.splittedDate[0] ?? '', Validators.required],
	  parent_name: [data.parent_name ?? '', ( this.underAge ? Validators.required : '')],
      parent_email: [data.parent_email ?? '', ( this.underAge ? Validators.required : '')],
      display_name: [data.display_name ?? '', Validators.required],
      country: [data.country ?? 0],
	  email: [data.email ?? '', [Validators.required, Validators.email]],
      address: [data.address ?? '', [Validators.required, Validators.pattern('^[a-zA-Z \-\']+')]],
      house_number: [data.house_number ?? '', [Validators.required, Validators.pattern('^[0-9 \-\']+')]],
      additional_address: [data.additional_address ?? '', Validators.required],
      postcode: [data.postcode ?? '', Validators.required],
      city: [data.city ?? '', [Validators.required, Validators.pattern('^[a-zA-Z \-\']+')]],
      telephone: [data.telephone ?? '', Validators.required],
      contact_person_name: [data.contact_person_name ?? '', Validators.required],
      cellphone: [data.cellphone ?? '', Validators.required],
      covid_option: [data.covid_option ?? ''],
      account_name: [data.account_name ?? ''],
	  account_number: [data.account_number ?? ''],
	  trip_fee: [data.trip_fee ?? ''],
	  cancellation_insurance: [data.cancellation_insurance ?? ''],
	  travel_insurance: [data.travel_insurance ?? ''],
      survival_adventure_insurance: [data.survival_adventure_insurance ?? ''],
	  insurance_admin_charges: [data.insurance_admin_charges ?? ''],
	  nature_disaster_insurance: [data.nature_disaster_insurance ?? ''],
	  sgr_contribution: [data.sgr_contribution ?? ''],
	  total_amount: [data.total_amount ?? ''],
	  paid_amount: [data.paid_amount ?? ''],
      contact_person_extra_name: [data.contact_person_extra_name ?? '', (this.showExtraContactPerson ? Validators.required : '')],
      contact_person_extra_cellphone: [data.contact_person_extra_cellphone ?? '', (this.showExtraContactPerson ? Validators.required : '')],
      child_diet: [data.child_diet ?? ''],
      child_medication: [data.child_medication ?? ''],
      about_child: [data.about_child ?? ''],
	  can_drive: [data.can_drive ?? ''],
	  have_driving_license: [data.have_driving_license ?? ''],
	  have_creditcard: [data.have_creditcard ?? ''],
      insurance: [[]],
      insurnace_question_1: [''],
	  can_pay_part: false,
      insurnace_question_2: [''],
	  location_pickup_id: [data.location_pickup_id ?? ''],
	  reservation_id: [''],
    },{
			validator: [PostCode('country', 'postcode'), CheckDate('dob_date', 'dob_month', 'dob_year')],
		});
  }
  validateReservationAccepted(){
    this.reservation_terms = true;
	if(this.reservation_terms){
		this.reservation_accepted = true;
	}
	this.reservation_validated = true;
  }
  showOptionTerms(){
	this.reservation_terms = false;
    this.reservation_accepted = false;
	this.reservation_validated = false;
  }
  tripEndDate(trip: any){
    return (new Date(trip.start_date)).setDate((new Date(trip.start_date)).getDate() + trip.duration);
  }
  calculateDiff(){
    let date = new Date(this.trip.start_date);
    let currentDate = new Date();

    let days = Math.floor((currentDate.getTime() - date.getTime()) / 1000 / 60 / 60 / 24);
    return days;
  }
  lastDate(){

	var lastdate: any = '';


		let selectedDate = new Date();
		lastdate = selectedDate.setDate( selectedDate.getDate() - 42 );
		lastdate = new Date(lastdate);

    return lastdate;
  }
	total_price(){
		let total = Number(this.trip.trip_fee) + Number(this.configurations.config.nature_disaster_insurance.value) + Number(this.configurations.config.sgr_bijdrage.value);
		//console.log($scope.settings.trip_fee);
		if( this.insurances.indexOf( 'Annuleringsverzekering' )!==-1 ) {
			total += Number(this.cancellation_insurance);
		}
		if( this.insurances.indexOf( 'Reisverzekering' )!==-1 ) {
			total += Number(this.travel_insurance);
		}
		if(this.insurances && (this.insurances.indexOf( 'Annuleringsverzekering' )!==-1 && this.insurances.indexOf( 'Reisverzekering' )!==-1)){
			total += Number(this.insurance_administration_charges);
		}
		return total;
	}
	deposit(){
		let total_deposit = this.total_price();

		if(this.calculateDiff() <= 42){
			this.bookingForm.can_pay_part = true;
		}
		if( this.bookingForm.can_pay_part  ) {
			total_deposit = this.total_price() * Number(this.configurations.config.deposit_percent.value)/100;

		}
		return total_deposit;
	}
  under_age(year: number){
    const age = (new Date().getFullYear() - year );
    // //display the calculated age
    this.underAge = age < 17 ? true : false;
    return age < 17 ? true : false;
  }

  getInsurances(isChecked: any, value: string){
    const index = this.insurances.findIndex((x:any) => x == value);
    if(isChecked.checked && index == -1){
      this.insurances.push(value);
    }
    else if(!isChecked.checked && index >= 0){
      this.insurances.splice(index, 1);
    }
	if((isChecked.checked && this.insurances) && (this.insurances.indexOf( 'Annuleringsverzekering' )!==-1 && this.insurances.indexOf( 'Reisverzekering' )!==-1)){
		this.insurance_administration_charges = this.insurance_administration_charges*2;
	}
	else{
		this.insurance_administration_charges = this.insurance_administration_charges;
	}
	console.log(this.insurances);
    this.bookingForm.get('insurance').setValue(this.insurances);
  }

  getTermsAndConditions(isChecked: any, value: string){
    const index = this.termsAndCondition.findIndex((x:any) => x == value);
    if(isChecked.checked && index == -1){
      this.termsAndCondition.push(value);
    }
    else if(!isChecked.checked && index >= 0){
      this.termsAndCondition.splice(index, 1);
    }
	console.log(this.termsAndCondition);
  }

  toggle(index: number){
    if(index != -1){
      if (!this.bookingForm.valid) return;
      this.activeLink.forEach((res: any, index: number) =>{
        this.activeLink[index] = false;
      })
      this.activeLink[index] = true;
    }
  }
	paymentLinks: any;
  checkAndConfirm(event: any, confirm: string){
	  console.log(this.termsAndCondition);
    event.preventDefault();
    this.isSubmitted = true;
	if (!this.bookingForm.valid) return;
    this.booking = this.bookingForm.value;
    this.booking.child_dob = (this.booking.dob_year + '-' + (parseInt(this.booking.dob_month)<=9?"0":'')+parseInt(this.booking.dob_month) + '-' + this.booking.dob_date);
	this.booking.trip_fee = this.trip.trip_fee;
	if( this.insurances.indexOf( 'Annuleringsverzekering' )!==-1 ) {
		this.booking.cancellation_insurance = this.cancellation_insurance;
	}
	if( this.insurances.indexOf( 'Reisverzekering' )!==-1 ) {
		this.booking.travel_insurance = this.travel_insurance;
	}
	this.booking.insurance_admin_charges = this.insurance_administration_charges;
	this.booking.nature_disaster_insurance = this.configurations.config.nature_disaster_insurance.value;
	this.booking.sgr_contribution = this.configurations.config.sgr_bijdrage.value;
	this.booking.total_amount = this.total_price();
	this.bookingForm.get('dob_month').setValue(parseInt(this.booking.dob_month) + 1);
	//this.booking.dob_month = this.booking.dob_month+1;

    this.activeLink.forEach((res: any, index: number) =>{
      this.activeLink[index] = false;
    })
    this.activeLink[1] = true;
	if( (confirm=="confirm") && (this.termsAndCondition.length >= 2)){

		if(this.type.reserve){
			this.booking.reserve = this.type.reserve;
		}
		else{
			this.booking.reserve = false;
		}
		console.log(this.booking);
		this.processing = true;
		this.bookingService.store(this.booking).subscribe((response: any) => {
		if(response.status){
			this.paymentLinks = response.paymentLinks;
			this.activeLink[1] = false;
			this.activeLink[2] = true;
			this.processing = false;
		}
	},
    (errorResponse: HttpErrorResponse) => {
      this.processing = false;
      this.errors = this.responseErrorComponent.responseError(errorResponse, this.booking);
    });
	}
  }



}
