import { Component } from '@angular/core';
import { AuthService } from '../../services/auth/auth.service';
import {DataService} from "../../services/data.service";

@Component({
  selector: 'app-forum',
  templateUrl: './forum.component.html',
  styleUrls: ['./forum.component.scss']
})
export class ForumComponent {

  configurations: any;
	pageLoading = false;
	authUser: any;
  forum: any;
  forum_list: any;
  pages:any = [];
  current_page: number = 0;
  last_page: number = 0;


  constructor(
		private authService: AuthService,
		private dataService: DataService
	) {}
  getForumData(search: any, pageNumber: number){
    if(!this.pageLoading){
      this.pageLoading = true;
      this.authService.getForum({search}, pageNumber).subscribe((response: any) => {
        this.pageLoading = false;
        this.forum = response;
        this.forum_list = response.data;
        this.current_page = response.current_page;
        this.last_page = response.last_page;
        this.pages = [];
        for (let i = 1; i <= response.last_page; i++) {
          this.pages.push(i);
        }
        this.dataService.setMeta({
          "title": "",
          "description": "",
          "keywords": "",
          "date": ''
        });
      });
    }
	}
  ngOnInit() {
		this.authService.authorize().then(result => {
			if (result) {
				this.authService.user.subscribe((response: any) => {
					this.authUser = response;
				})
			}
		});
		this.dataService.configurations.subscribe((configurations: any) => {
		  if(configurations.settings){
			this.configurations = configurations;
			this.getForumData('', 1);
		  }
		});
	}

  nextPage(pageNumber: number){
    this.getForumData('', pageNumber);
  }
}
