import { Component, Input, SimpleChanges, ViewChild, ViewContainerRef, ComponentFactoryResolver, Self, Injector } from '@angular/core';
import { DataService } from '../services/data.service';
import { DomSanitizer } from '@angular/platform-browser';
import { createCustomElement } from '@angular/elements';
import { TripSliderComponent } from '../common/trip-slider/trip-slider.component';
import { UpcomingTripsComponent } from '../common/upcoming-trips/upcoming-trips.component';

@Component({
  selector: 'app-page',
  templateUrl: './page.component.html',
  styleUrls: ['./page.component.scss']
})
export class PageComponent {
	@Input() pageId!: any;
	configurations: any = {};
	page: any = null;
	initDone = false;
	trips: any;

	pageLoading = false;

	components: any[] = [
        TripSliderComponent,
		UpcomingTripsComponent
    ];
	  
	constructor(private dataService: DataService,
		private sanitizer: DomSanitizer,
		private injector: Injector) {

	}

	ngOnInit(){
		const element = createCustomElement(TripSliderComponent, { injector: this.injector });
		const element1 = createCustomElement(UpcomingTripsComponent, { injector: this.injector });
    	customElements.define('app-trip-slider', element);
		customElements.define('app-upcoming-trips', element1);
		this.dataService.configurationsSubject.subscribe((configurations: any) => {
		  if(configurations.settings){
			this.configurations = configurations;
			this.initDone = true;
			this.getPageData();
		  }
		});
	}
	
	getPageData(){
		this.pageLoading = true;
		this.dataService.getPageData(this.pageId).subscribe((response: any) => {
			this.page = response;
			this.page.content = this.sanitizer.bypassSecurityTrustHtml(this.page.content);
			this.pageLoading = false;
			this.dataService.setMeta({
				"title": this.page.meta_title,
				"description": this.page.meta_description,
				"keywords": this.page.meta_keywords,
				"date": this.page.updated_at
			});
		});
	}

	ngOnChanges(changes: SimpleChanges) {
		if(this.initDone){
			this.getPageData();
		}
	}
}
