import { Component, ElementRef, OnInit, ViewChild } from '@angular/core';
import { FormControl } from '@angular/forms';
import { ActivatedRoute, Router, RouterState } from '@angular/router';
import { SupportService } from 'src/app/services/support.service';
import {DataService} from "../../services/data.service";
import { DomSanitizer, SafeResourceUrl } from '@angular/platform-browser';

@Component({
  selector: 'app-contact-single',
  templateUrl: './contact-single.component.html',
  styleUrls: ['./contact-single.component.scss']
})
export class ContactSingleComponent implements OnInit{
	configurations: any;
  support_categories: any;
  article: any = null;
  articles: any = null;
  blog: any = null;
  path:any;
  params: any;
  type: any;
  searchValue: any;
  pageLoading = false;
  categoryType = false;
  support_links: any = {};
  contact_details_title: any = {};
	contact_col_1: any = {};
	contact_col_2: any = {};
	contact_col_3: any = {};
	footer_partners_text: any = {};

  typeOf: any = '';
  @ViewChild('search') searchInp!: ElementRef;

  constructor(
    private route: ActivatedRoute,
    private router: Router,
    public supportService: SupportService,
    private dataService: DataService,
    private sanitizer: DomSanitizer
  ) {
    let getState = this.router.getCurrentNavigation();
    this.typeOf = getState?.extras.state;
    if(this.typeOf && this.typeOf.search){ this.typeOf = localStorage.setItem('contactPageSearch', this.typeOf.search)}
    if(localStorage.getItem('contactPageSearch')){ this.typeOf = localStorage.getItem('contactPageSearch') }
  }

  checkActiveLink(index: number, record: any, value: string){
    const rec = record.filter((x:any) => x.page.page_name === value);
    if(rec.length > 0) return true; return false;
  }

	ngOnInit(){
		this.dataService.configurationsSubject.subscribe((configurations: any) => {
			if(configurations.settings){
			  this.configurations = configurations;
			  this.support_links = this.configurations.config.support_links.value;
			  this.contact_details_title = this.configurations.config.contact_details_title.value;
			  this.footer_partners_text = this.configurations.config.fooer_partners_text.value;
			  this.contact_col_1 = this.configurations.config.contact_col_1.value;
			  this.contact_col_2 = this.configurations.config.contact_col_2.value;
			  this.contact_col_3 = this.configurations.config.contact_col_3.value;
			}
		});
		this.route.params.subscribe((paramse: any) => {
			this.params = Object.values(paramse);
			if(this.params.length > 0){
				const state: RouterState = this.router.routerState;
				if((this.typeOf) && (this.typeOf == 'search')){
				  this.type = "search";
				  this.path = (this.params[1]).replace(".html", "");
				}
				else{
					this.type = "article";
				  if((state.snapshot.url.split("/").length-1) == 2){
					this.type = "category";
					this.path = (this.params[0]).replace(".html", "");
				  }
				  if((state.snapshot.url.split("/").length-1) == 3){
					this.type = "article";
					this.path = (this.params[1]).replace(".html", "");
				  }
				}
			}
			this.getSingleArticle('', this.path);
		});

		this.supportService.getSupportArticleData('', '').subscribe((response: any) => {
		  if(response){
			this.support_categories = response;
		  }
		});


    }
	is_category = '';
	getSingleArticle(searchType: string, Pagename: any){
		if(!this.pageLoading){
		  if(searchType == 'search'){
			this.typeOf = localStorage.setItem('contactPageSearch', this.searchInp.nativeElement.value);
			this.type = 'search';
		  }
		  this.pageLoading = true;
		  
		  this.supportService.getSingleArticle(this.type, Pagename).subscribe((response: any) => {
			if(response.article){
			  this.article = response.article;
			}
			if(response.articles){
			  this.articles = response.articles;
			  this.is_category = response.is_category;
			}
			this.blog = response.blog;
			this.pageLoading = false;
			this.supportService.setMeta({
				"title": this.article.meta_title,
				"description": this.article.meta_description,
				"keywords": "",
				"date": ""
			});
		  });
		}
	}
	removeType(){
		localStorage.setItem('contactPageSearch', '');
		
	}

  
}
