import { Component, Input } from '@angular/core';
import { DataService } from '../services/data.service';
import Chart from 'chart.js/auto';
import { Router } from '@angular/router';
import { ModalService } from '../services/modal.service';

@Component({
  selector: 'app-location',
  templateUrl: './location.component.html',
  styleUrls: ['./location.component.scss']
})
export class LocationComponent {

  @Input() pageId!: any;
  configurations: any = {};
  page: any = null;
  selectedTrip = -1;
  locationTab = false;

  pageLoading = false;
  
  selectBox = true;
  infoBox = true;
  hidetretangle = true;
  chart: any;

  selectedIndex = -1;
  
  tab = 1;
  getTabData(tab: any){
	  this.tab = tab;
  }
  constructor(private dataService: DataService,
    private modalService: ModalService,
    private router: Router) {

  }

  ngOnInit(){
    this.dataService.configurationsSubject.subscribe((configurations: any) => {
      if(configurations.settings){
        this.configurations = configurations;
        this.getPageData();
      }
    });
    if (window.innerWidth <= 768) { // 768px portrait
        this.locationTab = true;
    }
  }

  getPageData(){
    this.pageLoading = true;
    this.dataService.getPageData(this.pageId).subscribe((response: any) => {
      this.page = response;
      this.pageLoading = false;
      this.dataService.setMeta({
        "title": this.page.meta_title,
        "description": this.page.meta_description,
        "keywords": this.page.meta_keywords,
        "date": this.page.updated_at
      });
      let chartData: any = [];
      let percentageTotal = 0;
      this.page.pageable.chart_values.map((chartLabel: any) => {
        let percentage = (parseInt(chartLabel)/this.page.pageable.chart_total_count)*100
        percentageTotal += percentage;
        chartData.push(percentage)
      });
      setTimeout(() => {
        this.chart = new Chart('ageGroupChart', {
          type: 'bar',
          data: {
            labels: this.page.pageable.chart_labels,
            datasets: [{
                data: chartData,
                backgroundColor: [
                    '#ec008c'
                ],
                borderColor: [
                    '#ec008c'
                ],
                borderWidth: 1
            }]
          },
          options: {
            scales: {
                y: {
                    beginAtZero: true,
                    ticks: {
                      callback: function(value: any){
                        return value+"%"
                      }
                    }
                }
            },
            plugins: {
                title: {
                    display: true,
                    text: 'Gemiddelde samenstelling'
                },
                legend: {
                    display: false,
                },
                tooltip: {
                  enabled: false
                }
            }
          }
      });
      }, 200);
      
    });
  }

  tripEndDate(trip: any){
    return (new Date(trip.start_date)).setDate((new Date(trip.start_date)).getDate() + trip.duration);
  }

  selectTrip(i: any){
    if(this.page.pageable.trips[i].space.remaining >= 1){
      this.selectedTrip = i;
    }
  }

  redirect(type: string){
    this.router.navigate(['/' + type + '/' + this.page.pageable.trips[this.selectedTrip].start_date + '/' + this.page.page_name + '.html']);
  }

  showDiv(index: number) {
    this.selectedIndex = index;
  }

  selectIcon(){
    this.selectBox = !this.selectBox;
  }
  infoIcon(){
    this.infoBox = !this.infoBox;
  }
  locationTabIcon(){
    this.locationTab = !this.locationTab;
  }
  rectangleIcon(){
    this.hidetretangle = !this.hidetretangle;
  }

  openModal(id: string) {
    this.modalService.open(id);
  }

  closeModal(id: string) {
      this.modalService.close(id);
  }
}
