import { Component } from '@angular/core';
import { FormControl } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { VakantiesService } from '../services/vakanties.service';
import {DataService} from "../services/data.service";


@Component({
  selector: 'app-vakanties',
  templateUrl: './vakanties.component.html',
  styleUrls: ['./vakanties.component.scss'],


})
export class VakantiesComponent {

	trips: any;
	tripTypes: any;
	search = new FormControl();
	allVanties = true;
	allFilter = true;
	Vertrekdatum = true;
	configurations: any;
	params: any;
	initDone = false;
	pageLoading = false;
	pageNumber = 0;
	perPage: number = 12;
	meta_title: any = {};
	meta_description: any = {};

  slideConfig = { dots: true, infinite: false, slidesToShow: 1, slidesToScroll: 1, autoplay: false, autoplaySpeed: 3000, responsive: [{ breakpoint: 900, settings: { slidesToShow: 1, slidesToScroll: 1, } }, { breakpoint: 800, settings: { slidesToShow: 1, slidesToScroll: 1, } }, { breakpoint: 600, settings: { slidesToShow: 1, slidesToScroll: 1, centerMode: true, } }] };
	allVantiesIcon(){
		this.allVanties = !this.allVanties;
	}
	allFilterIcon(){
		this.allFilter = !this.allFilter;
	}
	VertrekdatumIcon(){
		this.Vertrekdatum = !this.Vertrekdatum;
	}
	constructor(
		private vakantiesService: VakantiesService,
		private route: ActivatedRoute,
		private dataService: DataService,
		private router: Router,

	) {
	}

	ngOnInit(){
		this.getTripData('', 1);
		this.route.queryParams.subscribe((params: any) => {
		  this.initDone = true;
		  this.params = params;
		  this.dataService.configurationsSubject.subscribe((configurations: any) => {
			if(configurations.settings){
			  this.configurations = configurations;
			  this.meta_title = this.configurations.config.vakanties_meta_title.value;
			  this.meta_description = this.configurations.config.vakanties_meta_description.value;
			}
		  });
		});
		this.vakantiesService.getTripTypes().subscribe((response: any) => {
			this.tripTypes = response;
		});
	}
	next: any;
	getTripData(search: any, per_page: number){
		if(!this.pageLoading){
		  this.pageLoading = true;
		  this.perPage += per_page;
		  this.vakantiesService.getVakanties({search}, this.perPage).subscribe((response: any) => {
			this.trips = response.trip.data;
			this.perPage = response.trip.per_page;
			console.log(this.meta_title);
			console.log(this.meta_description);
			this.dataService.setMeta({
				"title": this.meta_title,
				"description": this.meta_description,
				"keywords": '',
				"date": ''
			});
			this.pageLoading = false;
		  });
		}
	}
	getIconImage(icon: string){
		let key = icon.toLowerCase().replace(" ", "_") + "_icon";
		return this.configurations.config[key].value;
	}


}
